h1,.button-container,.link-container{margin:2rem}@media (min-width: 576px){h1,.button-container,.link-container{margin:3rem}}.question-container{padding:2rem;cursor:pointer}@media (min-width: 576px){.question-container{padding:3rem}}.question-container:nth-child(even){background-color:#eeeeee}.question-container .question{font-weight:700;margin-bottom:1rem}.question-container .answer-container{margin-bottom:1rem;cursor:text}@media (min-width: 576px){.question-container .answer-container{margin-left:3rem}}.question-container .answer-container .answer{margin-bottom:1rem;min-height:21px}.question-container .answer-container .answer.answer--code{margin-bottom:0}.question-container .answer-container .answer.answer--code code{white-space:pre;font-size:10px;margin-bottom:0}@media (min-width: 576px){.question-container .answer-container .answer.answer--code code{font-size:medium;font-size:initial}}.question-container .answer-container img{max-width:100%;max-height:300px}.question-container .sources{word-wrap:break-word}.question-container .sources div{margin-bottom:1rem}.button-container{margin-left:0;margin-right:0}.link-container{font-size:10px}.unselectable{-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;margin:1rem 0;background-color:#eeeeff}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a,a:visited{color:blue}button{font-size:18px;font-weight:700;background-color:white;padding:1rem;width:50%;border:1px solid black;background-color:#eeeeee;border-radius:25px;cursor:pointer}ul{-webkit-padding-start:20px;padding-inline-start:20px}input[type="button"]{outline:none}input[type="button"]::-moz-focus-inner{border:0}*:focus{outline:0 !important}button:focus{border:none;outline:none}

